.comment {
  &__container {
    display: flex;
    position: relative;
    align-content: center;
    flex-direction: column;
    padding: 120px 0;

    @include vp-1199 {
      padding: 90px 0 55px;
    }

    @include vp-767 {
      padding: 90px 0 55px;
    }

    & h2 {
      font-family: $ff-alumni;
      font-size: 54px;
      line-height: normal;
      letter-spacing: 1.62px;
      text-align: center;
      padding-bottom: 37px;
      margin: 0;

      @include vp-1199 {
        font-size: 42px;
        letter-spacing: 1.26px;
        padding-bottom: 45px;
      }

      @include vp-767 {
        font-size: 32px;
        letter-spacing: 0.96;
      }
    }
  }

  &__wrapper {
    display: flex;
    margin: auto;
    max-width: 560px;
    min-height: 0;

    @include vp-767 {
      max-width: 225px;
    }
  }

  &__slide {
    display: grid;
    background-color: #1c3374;
    box-shadow: 0 0 40px rgba(28, 51, 116, 0.2);
    padding: 62px;
    grid-template-columns: 80px 1fr;
    column-gap: 30px;
    min-width: 0;

    @include vp-1199 {
      padding: 62px 65px;
    }

    @include vp-767 {
      padding: 40px 25px;
      display: flex;
      flex-direction: column;
    }
  }

  &__image {
    width: 80px;
    height: 80px;

    @include vp-767 {
      width: 68px;
      height: 68px;
    }
  }

  &__text {
    display: flex;
    flex-direction: column;
    color: $color-default-white;

    & h3 {
      font-size: 20px;
      font-weight: 700;
      padding-bottom: 10px;
      margin: 0;
    }

    & p {
      margin: 0;
    }
  }

  &__swiper-button svg {
    display: block;
    width: 16px;
    height: 32px;
  }

  &__swiper-button {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid $color-transparent;
    border-radius: 50%;
    width: 56px;
    height: 56px;
    top: 56.5%;

    @include vp-1199 {
      top: 58.5%;
    }

    @include vp-767 {
      top: 56%;
    }
  }

  &__swiper-button--prev {
    left: 24%;

    @include vp-1199 {
      left: 3%;
    }

    @include vp-767 {
      left: 0%;
    }
  }

  &__swiper-button--next {
    transform: rotate(180deg);
    left: 72%;

    @include vp-1199 {
      left: 90%;
    }

    @include vp-767 {
      left: 82%;
    }
  }

  &__swiper-button:hover {
    border: 1px solid $color-default-black;
    border-radius: 50%;
    width: 56px;
    height: 56px;
    background-position: 50%;
  }
}
