.questions {
  background-color: #f8f9fc;
  padding: 120px 103px 130px;
  margin: 0 auto;

  @media screen and (max-width: 1199px) {
    padding: 100px 45px 100px;
  }

  @media screen and (max-width: 767px) {
    padding: 70px 15px 60px;
  }

  & h2 {
    font-family: "Alumni Sans", "Helvetica", sans-serif;
    font-size: 54px;
    line-height: normal;
    letter-spacing: 1.62px;
    padding-bottom: 45px;
    margin: 0;

    @media screen and (max-width: 1199px) {
      font-size: 42px;
      letter-spacing: 1.26px;
      text-align: center;
    }

    @media screen and (max-width: 767px) {
      font-size: 32px;
      letter-spacing: 0.96px;
      padding-bottom: 30px;
    }
  }

  &__tabs {
    display: flex;
    column-gap: 40px;

    @media screen and (max-width: 1199px) {
      flex-direction: column;
      column-gap: 0;
      row-gap: 35px;
    }

    @media screen and (max-width: 767px) {
      row-gap: 15px;
    }
  }

  &__tabs-content {
    width: 100%;
    flex-grow: 1;
  }

  &__controls {
    display: flex;
    flex-direction: column;
    row-gap: 10px;

    @media screen and (max-width: 1199px) {
      flex-direction: row;
      column-gap: 10px;
      justify-content: center;
    }

    @media screen and (max-width: 767px) {
      flex-wrap: wrap;
    }
  }

    &__button:hover {
    border: 1px solid;
    border-image: linear-gradient(151deg, #ed0233 0%, #003eb7 100%);
    border-image-slice: 1;
    color: #ffffff;
    background: var(--1, linear-gradient(151deg, #ed0233 0%, #003eb7 100%));
  }

    &__button:active {
    border: 1px solid;
    border-image: linear-gradient(151deg, #ed0233 0%, #003eb7 100%);
    border-image-slice: 1;
    color: rgb(255, 255, 255, 0.6);
    background: var(--1, linear-gradient(151deg, #ed0233 0%, #003eb7 100%));
  }

  &__button.is-active {
    border: 1px solid;
    border-image: linear-gradient(151deg, #ed0233 0%, #003eb7 100%);
    border-image-slice: 1;
    color: #ffffff;
    background: var(--1, linear-gradient(151deg, #ed0233 0%, #003eb7 100%));
  }

  &__button {
    display: block;
    padding: 18px 20px;
    cursor: pointer;
    color: #102654;
    text-align: center;
    font-family: "Roboto Condensed", "Verdana", sans-serif;
    font-size: 18px;
    font-weight: 500;
    line-height: 100%;
    border: 1px solid rgba(16, 38, 84, 0.2);
    background-color: rgba(255, 255, 255, 0);
    min-width: 260px;
    user-select: none;

    @media screen and (max-width: 1199px) {
      min-width: 162px;
    }

    @media screen and (max-width: 767px) {
      min-width: 140px;
      padding: 14px 20px;
    }
  }
}
