.footer {
  padding: 40px 0 33px;

  color: $color-default-white;
  background-color: #030a1a;

  @include vp-1199 {
    padding: 45px 0 43px;
  }

  @include vp-767 {
    padding: 45px 0 50px;
  }
}

.footer__container {
  display: grid;
  grid-template-columns: 95px 1fr auto;
  gap: 20px;

  @include vp-767 {
    display: block;

    gap: 30px;
    columns: auto 2;
  }
}

.footer__logo {
  display: block;
  width: 88px;
  height: 32px;

  @include vp-1199 {
    width: 82px;
    height: 18px;
  }
  @include vp-767 {
    width: 68px;
    height: 15px;
    margin: 5px 0 21px;
  }
}

.footer__logo svg {
  width: 100%;
  height: 100%;
}

.footer__list {
  display: flex;
  flex-wrap: wrap;

  list-style: none;

  padding: 0;
  margin: 0 auto;

  @include vp-767 {
    display: block;

    margin-bottom: 54px;
  }
}

.footer__list-item {
  margin: 0 20px;

  @include vp-1199 {
    margin: 0 15px;
  }
  @include vp-767 {
    width: 100%;
    margin: 0 0 16px;
  }
}

.footer__list-link {
  font-size: 14px;
  line-height: 16px;
  color: $color-default-white;

  @include vp-767 {
    font-size: 16px;
    line-height: 19px;
  }
}

.footer__social {
  display: grid;
  grid-template-columns: repeat(3, auto);
  gap: 12px;

  margin: 0;
  padding: 0;

  list-style: none;

  @include vp-767 {
    justify-content: start;

    column-span: all;
  }
}

.footer__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;

  color: #030a1a;

  background-color: $color-default-white;
  border-radius: 50%;

  fill: currentColor;

  @include hover-focus {
    color: #ffffff;

    background-color: $color-egyptian-blue;
  }
}
